import cn from 'classnames';
import PropTypes from 'prop-types';
import ReactDom from 'react-dom';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { useHistory } from 'react-router';
import { motion, startAnimation, useMotionValue } from 'framer-motion';
import { FiX } from 'react-icons/fi';

// import { getContrastingColor } from '@mssgme/helpers';
import { renderNode, usePresenceRemoval } from '@mssgme/ui';

import { usePageTheme } from '../../hooks';

import styles from './ThemedModal.scss';

const noop = (e) => {
    e.stopPropagation();
};
const animateValue = (from, to, transition) => {
    void startAnimation('', from, to, transition);

    return {
        stop: () => from.stop(),
    };
};

export const ThemedModal = ({
    handleHash,
    compact,
    overlay,
    padding,
    fullWidth,
    children,
    showHeader,
    title,
    onClose,
    onClosing,
}) => {
    const history = useHistory();
    const y = useMotionValue('100%');
    const { theme } = usePageTheme();
    const style = useMemo(
        () => ({
            container: { y, backgroundColor: theme.colors.background, color: theme.colors.text },
            close: { color: theme.colors.text },
        }),
        [theme.colors]
    );
    // const contrastingColor = getContrastingColor(style.backgroundColor || '#fff', style.backgroundColor, style.color);
    const targetNode = document.querySelector('#page_root, #mainRoot');
    const [isClosing, setClosing] = useState(true);

    const handleClose = useCallback(
        async (...args) => {
            if (isClosing) {
                return;
            }

            setClosing(true);

            if (handleHash) {
                history.replace(history.location.pathname);
            }

            if (onClosing && false === (await onClosing())) {
                return;
            }

            animateValue(y, '100%', {
                duration: 0.3,
                type: 'ease',
                onComplete: () => {
                    if (!onClose) {
                        return;
                    }

                    if (onClose === true) {
                        history.goBack();
                    } else if (typeof onClose === 'string') {
                        history.push(onClose);
                    } else {
                        onClose(...args);
                    }
                },
            });
        },
        [isClosing, onClose, onClosing, handleHash]
    );

    useEffect(() => {
        const controls = animateValue(y, '0%', {
            duration: 0.3,
            type: 'ease',
            onComplete: () => setClosing(false),
        });

        return controls.stop;
    }, []);

    usePresenceRemoval();

    if (!targetNode) {
        return null;
    }

    return ReactDom.createPortal(
        <div className={styles.rootContainer} onClick={noop}>
            {(compact || overlay) && (
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    exit={{ opacity: 0 }}
                    transition={{ ease: 'easeInOut', duration: 0.3 }}
                    className={styles.overlay}
                    onClick={handleClose}
                />
            )}

            <motion.div
                className={cn(styles.root, compact && styles.compact, fullWidth && styles.fullWidth)}
                style={style.container}
            >
                {(showHeader || title) && (
                    <div className={styles.header}>
                        <div className={styles.closeButton} style={style.close} onClick={handleClose}>
                            <FiX size={24} />
                        </div>
                        {title && <div className={styles.title}>{title}</div>}
                        <div className={styles.closeButton} style={{ opacity: 0, pointerEvents: 'none' }}>
                            <FiX size={24} />
                        </div>
                    </div>
                )}

                <div className={cn(styles.content, padding && styles.padded)}>{renderNode(children, handleClose)}</div>
            </motion.div>
        </div>,
        targetNode
    );
};

ThemedModal.propTypes = {
    showHeader: PropTypes.bool,
    title: PropTypes.any,
    onClose: PropTypes.oneOfType([PropTypes.func, PropTypes.string, PropTypes.bool]).isRequired,
    onClosing: PropTypes.func,
    theme: PropTypes.shape({ backgroundColor: PropTypes.string, color: PropTypes.string }),
    compact: PropTypes.bool,
    handleHash: PropTypes.bool,
    overlay: PropTypes.bool,
    fullWidth: PropTypes.bool,
    padding: PropTypes.bool,
};

ThemedModal.defaultProps = {
    showHeader: true,
    fullWidth: true,
};
